<h1 id="written-examination" tabindex="-1"><a class="header-anchor" href="#written-examination" aria-hidden="true">#</a> Written Examination</h1>
<p>On this page you find information about the examination test <code>Written Examination</code>.</p>
<h2 id="number-of-credits" tabindex="-1"><a class="header-anchor" href="#number-of-credits" aria-hidden="true">#</a> Number of credits</h2>
<p>The <code>Written Examination</code> test is worth 2.5 credits. You will primarily prepare for it by completing the <RouterLink to="/courses/web-development-advanced-concepts/laboratory-work/">Laboratory Work</RouterLink> and the <RouterLink to="/courses/web-development-advanced-concepts/project-work/">Project Work</RouterLink>, so don't expect to use 2.5 credits of time studying for the <code>Written Examination</code> only.</p>
<h2 id="digital-exam" tabindex="-1"><a class="header-anchor" href="#digital-exam" aria-hidden="true">#</a> Digital Exam</h2>
<p>The <code>Written Examination</code> test is an Inspera exam; a digital exam you write on a school computer. More information about Inspera exams can be found on <a href="https://ju.se/student/en/digital-exam.html" target="_blank" rel="noopener noreferrer">the JU page <em>Digital Exam</em><OutboundLink/></a>.</p>
<h2 id="dates-re-examinations" tabindex="-1"><a class="header-anchor" href="#dates-re-examinations" aria-hidden="true">#</a> Dates/Re-examinations</h2>
<p>The <code>Written Examination</code> test is offered in:</p>
<ul>
<li>The exam period in March.</li>
<li>The re-exam period in July.</li>
<li>The re-exam period in August.</li>
</ul>
<p>When the date for these periods is approaching you can find more information about location and date at <a href="https://ju.se/student/en/studies/examination/exam-schedule-jth.html" target="_blank" rel="noopener noreferrer">the JU page <em>Exam Schedule JTH</em><OutboundLink/></a>.</p>
<h2 id="registration" tabindex="-1"><a class="header-anchor" href="#registration" aria-hidden="true">#</a> Registration</h2>
<p>As describe on the JU page <a href="https://ju.se/student/en/digital-exam.html" target="_blank" rel="noopener noreferrer">Digital Exam<OutboundLink/></a> you must register for the Inspera exam before you're allowed to write it.</p>
<div class="custom-container warning"><p class="custom-container-title">Re-register for re-examinations</p>
<p>You need to register for each written examination you want to attend. If you want to write a re-exam, you need to register for the re-exam even if you registered for the ordinary exam.</p>
</div>
<div class="custom-container danger"><p class="custom-container-title">The registration closes</p>
<p><strong>The registrations closes ~10 days before the exam starts</strong>, so register for it as soon as possible. However, the registration opens first ~1 month before the exam starts, so you can't register for it at course start.</p>
</div>
<h2 id="permitted-aids" tabindex="-1"><a class="header-anchor" href="#permitted-aids" aria-hidden="true">#</a> Permitted aids</h2>
<p>The only permitted aids at the <code>Written Examination</code> test are:</p>
<ul>
<li>A dictionary to translate to/from English from/to your native language.</li>
</ul>
<div class="custom-container danger"><p class="custom-container-title">About the computer</p>
<p>The computer you write the exam on may only be used to enter your answers on the exam. You may not use it to run other applications or search the web.</p>
</div>
<h2 id="grades-marking" tabindex="-1"><a class="header-anchor" href="#grades-marking" aria-hidden="true">#</a> Grades/Marking</h2>
<p>The exam consists of questions worth 30 points in total.</p>
<ul>
<li>For grade 3, 40% of max score (12 points) is required.</li>
<li>For grade 4, 60% of max score (18 points) is required.</li>
<li>For grade 5, 80% of max score (24 points) is required.</li>
</ul>
<p>The exam will be in English, but you may write your answers in either English or Swedish. If you write your answers in Swedish, make sure to not introduce any translation confusement. Write proper sentences (spelling, upper/lower case characters, punctuation, etc.). Answers that do not do this good enough/are vague/are partly incomprehensible cannot receive full score on the questions.</p>
<h2 id="sample-exam" tabindex="-1"><a class="header-anchor" href="#sample-exam" aria-hidden="true">#</a> Sample exam</h2>
<p>You can look at <RouterLink to="/courses/web-development-advanced-concepts/exams/exam-2021-08-22/">the exam from 2021-08-22</RouterLink> to get some idea about what the questions on your exam will be.</p>
